* {
    margin: 0;
    padding: 0;
}
body {
    background-color: #2f3640;
}

.container{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.box {
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: center;
    align-items: center;
    background-color: #353b48;
    margin-top: 50px;
    padding: 30px;
    border-radius: 10px;
    border: solid 2px #e1b12c;
}
.img {
    width: 250px;
    height: 250px;
    margin-top: 50px;
}

.textoFrase {
    font-style: italic;
    text-align: center;
    color:azure;
    font-size: 25px;
    margin-top: 25px;
    margin-bottom: 25px;
}

.copy {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
    color: azure;
    font-size: 18px;
}

a {
    text-decoration: none;
    color: azure;
    padding: 5px;
}

a:hover {
    background-color: #e1b12c;
    border-radius: 7px;
}

h1, h2 {
    color: azure;
    text-align: center;
}