.containerBtn {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 70px;
    width: 170px;
}

.btn {
    font-size: 20px;
    background-color: #e1b12c;
    color: #fff;
    border: none;
    height: 60px;
    width: 160px;
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 8px;
}

.btn:hover {
    background-color: #fbc531 ;
}

.btn:active {

    height: 50px;
    width: 145px;
}

